.navbar {
    background-color: #343353;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;    
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #212035;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    /* opacity: 0.5; */
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
    opacity: 0.6;
    transition: 0.5s;
    

  }
  
  .nav-text a:hover {
    background-color: #e25771;
    opacity: 1;
  }

  .nav-text a span{
    /* background-color: #e25771; */
    opacity: 1;    
    transition: 0.5s;
  }




  .nav-text a:hover span{
    /* background-color: #e25771; */
    opacity: 1;    
    transition: 0.5s;
  }

  
  .nav-menu-items {
    width: 100%;  
  }
  
  .navbar-toggle {
    /* background-color: #343353; */
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;    
  }
  
  span {
    margin-left: 16px;    
  }

  svg{
    fill: #fff;
    /* background-color: #343353; */
  }
  
  svg :hover{
    /* background-color: #e25771; */

  }
