.progress1, .progress2, .progress3, .progress4 {
    background: rgba(255,255,255,0.1);
    justify-content: flex-start;
    border-radius: 100px;
    align-items: center;
    position: relative;
    padding: 0 5px;
    display: flex;
    height: 30px;
    width: 40vw;
  }
  
  .progress-value1 {
    -webkit-animation: load1 3s normal forwards;
            animation: load1 3s normal forwards; 
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #e25771;
    height: 20px;
    width: 0;
  }
  
  @-webkit-keyframes load1 {
      0% { width: 0; }
      100% { width: 80%; }
    }
  
  @keyframes load1 {
      0% { width: 0; }
      100% { width: 80%; }
    }
    
    .progress-value2 {
        -webkit-animation: load2 3s normal forwards;
                animation: load2 3s normal forwards; 
        box-shadow: 0 10px 40px -10px #fff;
        border-radius: 100px;
        background: #52c8b9;
        height: 20px;
        width: 0;    
    }
  
  @-webkit-keyframes load2 {
    0% { width: 0; }
    100% { width: 89%; }
  }
  
  @keyframes load2 {
    0% { width: 0; }
    100% { width: 89%; }
  }

  .progress-value3 {
    -webkit-animation: load3 3s normal forwards;
            animation: load3 3s normal forwards; 
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #fff;
    height: 20px;
    width: 0;    
}

@-webkit-keyframes load3 {
0% { width: 0; }
100% { width: 80%; }
}

@keyframes load3 {
0% { width: 0; }
100% { width: 80%; }
}

.progress-value4 {
    -webkit-animation: load4 3s normal forwards;
            animation: load4 3s normal forwards; 
    box-shadow: 0 10px 40px -10px #fff;
    border-radius: 100px;
    background: #6967a4;
    height: 20px;
    width: 0;    
}

@-webkit-keyframes load4 {
0% { width: 0; }
100% { width: 98%; }
}

@keyframes load4 {
0% { width: 0; }
100% { width: 98%; }
}

